//#region > Imports
//> React
// Contains all the functionality necessary to define React components
import React from "react";
//> React Markdown
import ReactMarkdown from "react-markdown";
//> MDB
// "Material Design for Bootstrap" is a great UI design framework

//> CSS
import "./customcard.scss";
import {
  MDBContainer,
  MDBCard,
  MDBIcon,
  MDBCardBody,
  MDBCardFooter,
  MDBBadge,
  MDBCol,
  MDBRow,
} from "mdbreact";
//#endregion

//#region > Components
/**
 * @class A card of a Kanban board
 */
class CustomCard extends React.Component {
  render() {
    console.log(this.props);
    return (
      <MDBContainer id="customCard" onClick={this.props.onClick}>
        <MDBCard>
          <MDBCardBody>
            <MDBRow>
              <MDBCol md="10">
                <b>{this.props.title}</b>
              </MDBCol>
              <MDBCol md="2">
                <MDBIcon icon="trash-alt" onClick={this.props.onDelete} />
              </MDBCol>
            </MDBRow>
            <MDBRow>
              <MDBCol md="10">
                <ReactMarkdown source={this.props.description} />
              </MDBCol>
            </MDBRow>
          </MDBCardBody>
          <MDBCardFooter>
            {this.props.tags.length > 0 &&
              this.props.tags.map((tag) => (
                <MDBBadge color={tag.color}>
                  <MDBIcon icon={tag.icon} />
                  {tag.name}
                </MDBBadge>
              ))}
          </MDBCardFooter>
        </MDBCard>
      </MDBContainer>
    );
  }
}

//#endregion

//#region > Exports
//> Default Class
export default CustomCard;
//#endregion

