//#region > Imports
//> React
// Contains all the functionality necessary to define React components
import React from "react";

//> Kanban Board
import Board from "react-trello";

//> CSS
import "./kanbanpage.scss";

//> CustomCard
import {
  CustomCard,
  AddCardLink,
  NewLaneForm,
  NewCardForm,
  AddCardModal,
} from "../../atoms";
//#endregion

let data = require("./data.json");

//#region > Components
/** @class This component displays the Kanban board */
class KanbanPage extends React.Component {
  state = {
    showModal: false,
    card: null,
  };

  toggle = () => [
    this.setState({
      showModal: !this.state.showModal,
    }),
  ];

  handleCardClick = (id) => {
    console.log(id, "ID");
    let card = data.lanes.map((lane) => {
      console.log(lane.cards, "ID");
      return lane.cards.find((card) => card.id == id);
    })[0];

    this.setState({ card });
    this.toggle();
  };

  handleAdd = (card) => {
    data.lanes = data.lanes.map((lane) => {
      lane.cards = lane.cards.map((oldCard) => {
        if (oldCard.id === card.id) {
          return card;
        }

        return oldCard;
      });

      return lane;
    });

    this.toggle();
  };

  render() {
    return (
      <>
        <Board
          data={data}
          components={{
            Card: CustomCard,
            AddCardLink: AddCardLink,
            NewLaneForm: NewLaneForm,
            NewCardForm: NewCardForm,
          }}
          draggable
          laneDraggable
          editable
          canAddLanes
          editLaneTitle
          onCardClick={(id) => this.handleCardClick(id)}
        />
        {this.state.showModal && (
          <AddCardModal
            toggle={this.toggle}
            card={this.state.card}
            handleAdd={this.handleAdd}
          />
        )}
      </>
    );
  }
}
//#endregion

//#region > Exports
//> Default Class
export default KanbanPage;
//#endregion

