//#region > Imports
//> React
// Contains all the functionality necessary to define React components
import React from "react";
//> MDB
// "Material Design for Bootstrap" is a great UI design framework
import {
  MDBModal,
  MDBModalHeader,
  MDBCol,
  MDBModalBody,
  MDBRow,
  MDBIcon,
  MDBSelect,
  MDBBadge,
  MDBBtn,
} from "mdbreact";

//> CSS
import "./addcardmodal.scss";
//#endregion

//#region > Components
/** @class  */
class AddCardModal extends React.Component {
  constructor(props) {
    super(props);

    console.log(this.props, "CARD");
    if (this.props.card) {
      let { card } = this.props;

      this.state.id = card.id;
      this.state.title = card.title;
      this.state.description = card.description;
      this.state.tags = card.tags;
    }
  }

  state = {
    loading: false,
    id: undefined,
    title: "",
    description: "",
    tags: [],
    options: [
      {
        text: (
          <MDBBadge color="info-color">
            <MDBIcon icon="star" /> Feature
          </MDBBadge>
        ),
        value: { name: "Feature", icon: "star", color: "info-color" },
      },
      {
        text: (
          <MDBBadge color="success-color">
            <MDBIcon icon="exclamation" /> Requirement
          </MDBBadge>
        ),
        value: {
          name: "Requirement",
          icon: "exclamation",
          color: "success-color",
        },
      },
      {
        text: (
          <MDBBadge color="secondary-color">
            <MDBIcon icon="book" /> Documentation
          </MDBBadge>
        ),
        value: {
          name: "Documentation",
          icon: "book",
          color: "secondary-color",
        },
      },
      {
        text: (
          <MDBBadge color="warning-color-dark">
            <MDBIcon icon="bug" /> Bug
          </MDBBadge>
        ),
        value: { name: "Bug", icon: "bug", color: "warning-color-dark" },
      },
    ],
  };

  handleSelectChange = (val) => {
    this.setState({
      tags: val,
    });
  };

  handleTextChange = (e) => {
    this.setState({
      [e.target.name]: e.target.value,
    });
  };

  addCard = () => {
    let card = {
      title: this.state.title,
      description: this.state.description,
      tags: this.state.tags,
    };

    if (this.state.id) {
      card.id = this.state.id;
    }

    this.props.handleAdd(card);
  };

  render() {
    return (
      <MDBModal
        modalStyle="white"
        className="text-dark"
        size="lg"
        id="addcard"
        backdrop={true}
        isOpen={true}
        toggle={this.props.toggle}
      >
        <MDBModalHeader
          className="text-center text-dark donate"
          titleClass="w-100"
          tag="p"
        >
          Add Card
        </MDBModalHeader>
        <MDBModalBody>
          <div className="card-data">
            <p className="font-weight-bold">Card title</p>
            <MDBRow>
              <MDBCol md="6">
                <input
                  type="text"
                  name="title"
                  className="form-control"
                  onChange={this.handleTextChange}
                  value={this.state.title}
                  placeholder="Title"
                />
              </MDBCol>
            </MDBRow>
            <p className="font-weight-bold">Card description</p>
            <MDBRow>
              <MDBCol md="6">
                <textarea
                  type="text"
                  name="description"
                  className="form-control"
                  onChange={this.handleTextChange}
                  value={this.state.description}
                  placeholder="Description"
                />
              </MDBCol>
            </MDBRow>
            <p className="font-weight-bold">Tags</p>
            <MDBRow>
              <MDBCol md="6">
                <MDBSelect
                  multiple
                  options={this.state.options}
                  getValue={this.handleSelectChange}
                  selected="Choose your tags"
                  selectAll
                />
              </MDBCol>
            </MDBRow>
            <MDBRow>
              <MDBBtn onClick={this.addCard}>Add Card</MDBBtn>
            </MDBRow>
          </div>
        </MDBModalBody>
      </MDBModal>
    );
  }
}
//#endregion

//#region > Exports
//> Default Class
export default AddCardModal;
//#endregion

