//#region > Imports
//> React
// Contains all the functionality necessary to define React components
import React from "react";
//> MDB
// "Material Design for Bootstrap" is a great UI design framework

import AddCardModal from "../AddCardModal";
//#endregion

//#region > Components
/**
 * @class A card of a Kanban board
 */
class NewCardForm extends React.Component {
  state = {
    show: true,
  };

  toggle = () => {
    this.setState({
      show: !this.state.show,
    });
    this.props.onCancel();
  };

  handleAdd = (card) =>
    this.props.onAdd({
      ...card,
    });

  render() {
    return (
      <>
        {this.state.show && (
          <AddCardModal toggle={this.toggle} handleAdd={this.handleAdd} />
        )}
      </>
    );
  }
}

//#endregion

//#region > Exports
//> Default Class
export default NewCardForm;
//#endregion

